<template>
  <div class="header">
    <el-row>
      <el-col :span="24">
        <el-row :gutter="20">
          <el-col :span="12" :offset="6" justify="center" align="center">
            <div class="LoginBox">
              <div>
                <h3>标题VUE3.0</h3>
                <el-form
                  ref="ruleFormRef"
                  :model="ruleForm"
                  status-icon
                  :rules="rules"
                  class="demo-ruleForm"
                >
                  <el-form-item prop="pass">
                    <el-input
                      v-model="ruleForm.user"
                      placeholder="请输入账户"
                      type="text"
                      autocomplete="off"
                      size="large"
                    />
                  </el-form-item>
                  <el-form-item prop="checkPass">
                    <el-input
                      v-model="ruleForm.pass"
                      type="password"
                      autocomplete="off"
                      placeholder="请输入密码"
                      size="large"
                    />
                  </el-form-item>
                  <el-form-item>
                    <el-button
                      type="primary"
                      size="large"
                      @click="submitForm(ruleForm)"
                      >登录</el-button
                    >
                  </el-form-item>
                </el-form>
              </div>
            </div>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Storage from "../../utils/DeviceStorage";
import Message from '../../utils/Message'
export default {
  name: "login",
  data() {
    return {
      ruleForm: {},
    };
  },
  methods: {
    submitForm(form) {
      if (form.user == "" || form.pass == "") {
        Message.warning("请输入账户或密码")
        return;
      }
      if(form.user === 'admin' && form.pass === '123456') {
          Message.success("登陆成功")
        Storage.set("yuelian", "1");
        this.$router.push({path:"/Home"})
      }else{
          Message.error("账户或密码错误")
      }
    },
  },
};
</script>

